import { TabsPrimitive } from '@vanilla-primitives/core';
import { cn } from '../utils';

export class TabsList extends TabsPrimitive.List {
	connectedCallback() {
		console.log("Connecting tabs")
		this.className = cn('tabs--list', this.className);
		super.connectedCallback();
	}
}

export class TabsTrigger extends TabsPrimitive.Trigger {
	connectedCallback() {
		this.className = cn('tabs--trigger', this.className);
		super.connectedCallback();
	}
}

export class TabsContent extends TabsPrimitive.Content {
	connectedCallback() {
		this.className = cn('tabs--content', this.className);
		super.connectedCallback();
	}
}

// Export the root component
export const Tabs = TabsPrimitive.Root;
